html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
  outline-offset: 0;
}

* {
  font-family: "Red Hat Text", sans-serif;
  font-weight: 700;
}

* {
  box-sizing: border-box;
  scrollbar-width: none;
  box-sizing: border-box;
  font-size: 100%;
}

*::selection {
  background-color: rgba(109, 114, 222, 0.5);
  color: #fff;
  opacity: 0.99;
}

*::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
}

.launch-countdown-timer .countdown__bottom::after, .launch-countdown-timer .countdown__bottom::before, .launch-countdown-timer .countdown__top > div::before, .launch-countdown-timer .countdown__top::before {
  display: flex;
  height: 100%;
  line-height: 0;
  justify-content: center;
  position: absolute;
  width: 100%;
}

.launch-countdown-timer .countdown__bottom::before, .launch-countdown-timer .countdown__top::before {
  background: radial-gradient(0.8rem circle at 0 100%, #141419 50%, transparent 50.2%), radial-gradient(0.8rem circle at 100% 100%, #141419 50%, transparent 50.2%);
}

.launch-countdown-timer .countdown__bottom::after, .launch-countdown-timer .countdown__top > div::before {
  background: radial-gradient(0.8rem circle at 0 0, #141419 50%, transparent 50.2%), radial-gradient(0.8rem circle at 100% 0, #141419 50%, transparent 50.2%);
}

.launch-countdown-timer .countdown__bottom::before, .launch-countdown-timer .countdown__top::before {
  align-items: flex-end;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: #2C2C44;
  color: #D64E72;
  overflow: hidden;
}

.launch-countdown-timer .countdown__bottom::after, .launch-countdown-timer .countdown__top > div::before {
  align-items: flex-start;
  background-color: #2C2C44;
  border-radius: 0 0 0.5rem 0.5rem;
}

@keyframes twinkle {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}
@keyframes flip {
  to {
    transform: rotateX(-179deg);
  }
}
.launch-countdown-timer .stars__star {
  width: 3px;
  height: 3px;
  border-radius: 6px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background: #8385A9;
  opacity: 0.9;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.25);
  animation: linear infinite twinkle;
  opacity: 1;
}
.launch-countdown-timer .page-wrapper {
  background-color: #1e1f29;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  min-height: 700px;
}
.launch-countdown-timer .title {
  color: #FFFFFF;
  font-size: 22px;
  letter-spacing: 0.5rem;
  text-transform: uppercase;
  text-align: center;
  margin: 132px 40px 104px 40px;
  line-height: 1.5;
}
.launch-countdown-timer .countdown {
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 5;
}
.launch-countdown-timer .countdown__container {
  display: flex;
  margin-bottom: 2rem;
  margin: 0 auto;
  z-index: inherit;
}
.launch-countdown-timer .countdown__parent {
  color: #fb6087;
  font-size: 80px;
  height: 140px;
  perspective: 500px;
  position: relative;
  transform-style: preserve-3d;
  width: 148px;
  margin-right: 32px;
}
.launch-countdown-timer .countdown__parent:last-child {
  margin-right: 0px;
}
.launch-countdown-timer .countdown__parent--animate .countdown__top > div,
.launch-countdown-timer .countdown__parent--animate .countdown__bottom::before {
  animation: flip 0.9s ease-in-out forwards;
  transform-origin: bottom;
}
.launch-countdown-timer .countdown__parent::before {
  content: "";
  background-color: #141419;
  border-radius: 0.5rem;
  height: 100%;
  position: absolute;
  top: 6%;
  width: 100%;
}
.launch-countdown-timer .countdown__top, .launch-countdown-timer .countdown__bottom {
  height: 50%;
  position: relative;
  width: 100%;
}
.launch-countdown-timer .countdown__top::before {
  content: attr(data-back);
  z-index: 5;
}
.launch-countdown-timer .countdown__top > div {
  height: 100%;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 10;
}
.launch-countdown-timer .countdown__top > div::before {
  content: attr(data-back);
  transform: rotate(180deg) rotateY(180deg);
}
.launch-countdown-timer .countdown__bottom::before {
  content: attr(data-front);
  backface-visibility: hidden;
  top: -100%;
  z-index: 20;
}
.launch-countdown-timer .countdown__bottom::after {
  content: attr(data-front);
  overflow: hidden;
}
.launch-countdown-timer .countdown__text {
  bottom: -3.5rem;
  color: #8885a0;
  font-size: 14px;
  letter-spacing: clamp(2px, 0.45vw, 0.5rem);
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}
.launch-countdown-timer .footer {
  align-self: flex-end;
  margin-top: auto;
  padding: 24px 0 40px;
  background: url("../assets/graphics/pattern-hills.svg") no-repeat bottom;
  width: 100%;
  height: 200px;
  background-size: 100% 200px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  background-position: bottom;
}
.launch-countdown-timer .footer__socialmedia,
.launch-countdown-timer .footer .socialmedia {
  align-self: flex-end;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
.launch-countdown-timer .footer__socialmedia__link:not(:last-of-type),
.launch-countdown-timer .footer .socialmedia__link:not(:last-of-type) {
  padding-right: 30px;
}
.launch-countdown-timer .footer__socialmedia__link svg path,
.launch-countdown-timer .footer .socialmedia__link svg path {
  transition: fill 0.25s ease;
}
.launch-countdown-timer .footer__socialmedia__link:hover svg path,
.launch-countdown-timer .footer .socialmedia__link:hover svg path {
  fill: #fb6087;
}
@media (max-width: 750px) {
  .launch-countdown-timer .page-wrapper {
    min-height: 600px;
  }
  .launch-countdown-timer .title {
    font-size: 18px;
    max-width: 320px;
    margin: 80px 20px 80px 20px;
  }
  .launch-countdown-timer .countdown__parent {
    font-size: 36px;
    height: 70px;
    width: 80px;
  }
  .launch-countdown-timer .countdown__text {
    font-size: 7px;
    bottom: -24px;
  }
}
@media (max-width: 500px) {
  .launch-countdown-timer .page-wrapper {
    min-height: 750px;
  }
  .launch-countdown-timer .page-wrapper .title {
    font-size: 18px;
    max-width: 320px;
    margin: 40px 20px 40px 20px;
  }
  .launch-countdown-timer .page-wrapper .countdown__container {
    flex-direction: column !important;
  }
  .launch-countdown-timer .page-wrapper .countdown__parent {
    margin-right: 0;
    margin-bottom: 48px;
  }
  .launch-countdown-timer .page-wrapper .footer {
    height: 120px;
    background-size: 100% 120px;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: bottom;
    padding: 20px 0;
  }
}